export class ModuleLoadedOnceGuard {

    constructor(targetModule: any) {
        if (targetModule) {
            throw new Error(`${targetModule.constructor.name} has already been loaded. Import this module in the AppModule only. Consider using a shared module instead.`);
        }
    }

}


