import { Component } from '@angular/core';
import { DialogComponent, DialogService } from "ng2-bootstrap-modal";

export interface CustomerSelectionModel {
    title: string;
    message: string;
}

@Component({
    selector: 'ta-customer-selection',
    template: require('./customer-selection-modal.component.html'),
    styles: [require('./customer-selection-modal.component.scss')]
})
export class CustomerSelectionModalComponent extends DialogComponent<CustomerSelectionModel, boolean> implements CustomerSelectionModel {
    title: string;
    message: string;
    constructor(dialogService: DialogService) {
        super(dialogService);
    }
    confirm() {
        this.result = true;
        this.close();
    }
}



